  Josh's Custom CSS Reset
  https://www.joshwcomeau.com/css/custom-css-reset/
*/

*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

@media (prefers-reduced-motion: no-preference) {
  html {
    interpolate-size: allow-keywords;
  }
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

p {
  text-wrap: pretty;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

#root,
#__next {
  isolation: isolate;
}
@font-face {
  font-family: "open sans";
  src:
    url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format("woff2"),
    url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format("woff");
  font-weight: normal;
  font-style: normal;
}
:root {
  --error-color: #ff525d;
  --form-color: #add8e6;
  --valid-color: #2e8b57;
  font-family: "open sans";
}
.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
form {
  outline: 1px solid;
  display: flex;
  flex-direction: column;
  justify-content: baseline;
  align-items: left;
  padding: 25px;
  border-radius: 20px;
  background-color: var(--form-color);
}
h1 {
  margin-bottom: 10px;
}
input {
  width: 100%;
  padding-top: 5px;
  border-radius: 10px;
  height: 28px;
  border: none;
  border: 2px solid white;
}
input:invalid:not(:placeholder-shown),
input:focus:invalid:not(:placeholder-shown),
input:active:invalid:not(:placeholder-shown) {
  border-color: var(--error-color);
}
input:valid:not(:placeholder-shown),
input:focus:valid:not(:placeholder-shown),
input:active:valid:not(:placeholder-shown) {
  border-color: var(--valid-color);
}
input:focus,
input:active {
  outline: none;
  border: solid black;
}
.error-message {
  background-color: var(--error-color);
  color: white;
  padding: 2px;
  padding-left: 10px;
  width: 100%;
  border-radius: 10px;
  margin-bottom: 5px;
}
.hidden {
  visibility: hidden;
}
.disappear {
  display: none;
}
p {
  font-size: 0.7rem;
  font-style: italic;
  margin-top: 5px;
  margin-bottom: 5px;
}
button {
  border-radius: 20px;
  border: none;
  cursor: pointer;
}
button:hover {
  background-color: lightgray;
  color: white;
}
span {
  color: var(--error-color);
  position: relative;
  top: 3px;
  left: 2px;
}
